import React from 'react';

export default class Subtitle extends React.Component {
    state = {};
    constructor (data) {
        super(data);
        this.state = {
            buttons: data.buttons || [],
            subtitleText: data.subtitle || "Subtitle",
        };
        this.getSubtitle = this.getSubtitle.bind(this)
    };

    getSubtitle () {
        return this.state.subtitleText
    }

    getSubtitlePagination () {
        return "0/"+this.state.buttons.length
    }

    render() {
        let subtitleText = this.getSubtitle()
        let subtitlePagination = this.getSubtitlePagination()
        return (      
            <div id="menuSubtitle" className="unselectable">
                <span className="menuSubtitleText">{subtitleText}</span>
                <span className="menuSubtitlePagination">{subtitlePagination}</span>
            </div>
        )
    }
}